export default {
	data() {
		return {
			id:0,
			list: [],
		}
	},
	onLoad(e) {
		if(!e.detail){
			return uni.showToast({
				title: '非法参数',
				icon: 'none'
			});
		}
		this.id = (JSON.parse(e.detail)).id
		this.getShipInfo()
	},
	methods: {
		getShipInfo(){
			let arr = {
				1:"在途中",
				2:"派件中",
				3:"已签收 ",
				4:"派送失败(拒签等)"
			}
			this.$H.get('/order/'+this.id+'/get_ship_info',{},{
				token:true
			}).then(res=>{
				let status = arr[res.result.deliverystatus]
				if(status){
					uni.setNavigationBarTitle({
						title:status
					})
				}
				this.list = res.result.list
			})
		}
	}
}
